.card-main{
  margin: 1% 0%;
  justify-content: right;
  width: 85%;
  background-color: #c7c8c8;
  border-radius: 8px;
  opacity: 0;
  // animation: 1.8s appear-card-ex 1;
  // animation-fill-mode: forwards;
}

.header{
  margin: auto;
  width: 98%;
  text-align: center;
  padding: 1.5% 0 1% 0;
  border-bottom: 2px solid #20aa2d;
  font-size: 1.7rem;
  font-weight: bold;
  color: #20aa2d;
}

.body{
  background-color: whitesmoke;
  font-size: 1.1rem;
  padding: 2%;
  text-align: justify;
}

.footer{
  text-align: center;
  padding: 1% 0 1% 0;
  font-size: 1.15rem;
}

@keyframes appear-card-ex {
	0%{
		opacity: 0;
	}
	100%{
		opacity: 1;
    transform: translateX(0px);
	}
}